function GenerateImg() {
    const location = useLocation();
    const { selectedData, selectedIndex, fullData } = location.state || {};
    const [images, setImages] = useState([]); // 생성된 이미지 URL 배열
    const [loading, setLoading] = useState(false); // 로딩 상태
    const [loading2, setLoading2] = useState(false); // 로딩 상태
    const [error, setError] = useState(null); // 에러 메시지
    const [activeMeaningIndex, setActiveMeaningIndex] = useState(null); // 활성화된 뜻의 index
    const [activeSentenceIndex, setActiveSentenceIndex] = useState(null); // 활성화된 예문의 index
    const [selectedMeaning, setSelectedMeaning] = useState(null); // 선택된 뜻
    const [selectedSentence, setSelectedSentence] = useState(null); // 선택된 예문
    const [selectedImage, setSelectedImage] = useState(null); // 선택된 이미지 URL
    const [overlay, setOverlay] = useState(false); // 오버레이 상태
    const [newSentence, setNewSentence] = useState(''); // 새 예문 상태 > 빈 문자열로 초기화
    const [newSentences, setNewSentences] = useState([]);

    const { apiKey } = useApiKey() || {};

    // console.log(selectedData);
    // console.log(selectedIndex);

    const [currentIndex, setCurrentIndex] = useState(selectedIndex);
    const [currentData, setCurrentData] = useState(selectedData);
    const [allData, setAllData] = useState(fullData); // location에서 전달받은 fullData 초기화
    // console.log(allData);

    //번호 넘길 때 데이터 업데이트 ----------------------------------------------------------------------------------------------
    useEffect(()=>{
        if (allData && currentIndex >= 0 && currentIndex < allData.length) {
            setCurrentData(allData[currentIndex]);
            setNewSentences([]);
        }
    },[currentIndex])

    //이미지 생성하기 ----------------------------------------------------------------------------------------------
    const generateImages = async () => {
        if (!currentData) {
            setError('단어가 전달되지 않았습니다.');
            return;
        }
    
        setLoading(true);
        setError(null);
        setImages([]);
    
        try {
            const imageRequests = [];
            const numImages = 4; // 생성할 이미지 개수
    
            for (let i = 0; i < numImages; i++) {
                const prompt = `
                    You are an expert at creating detailed, colorpencil cartoon illustration image for AI-generated artwork. Your image should include the following details:
                    - The central subject or scene that represents the meaning of the word.
                    - The setting or background where the word might naturally appear.
                    - Ensure the image is clear, specific, and focuses on accurately depicting the word's meaning.
                    - Avoid adding any text or words in the image.
                    ${i === 2 ? "- Picture description focuses on the previous and subsequent actions in the sentence." 
                        : i === 3 ? "- Visually explains words in an easy-to-understand way, focusing on relationships." 
                        : ""}
                    Now, generate an image for the given word: "${currentData.단어}".
                    ${selectedMeaning ? `- The meaning of this word is '${selectedMeaning}'. ` : ''}
                    ${selectedSentence ? `- An example sentence for this word is: '${selectedSentence}'. ` : ''}
                `;
    
                console.log(prompt)

                imageRequests.push(
                    axios.post(
                        'https://api.openai.com/v1/images/generations',
                        {
                            model: "dall-e-3",
                            prompt,
                            n: 1,
                            size: '1024x1024',
                            response_format: "b64_json" // Base64 형식으로 이미지 요청
                        },
                        {
                            headers: {
                                'Content-Type': 'application/json',
                                Authorization: `Bearer ${apiKey}`,
                            },
                        }
                    )
                );
            }
    
            const responses = await Promise.all(imageRequests);
    
            // Base64 데이터 추출
            const base64Images = responses.flatMap(res => res.data.data.map(img => img.b64_json));
            setImages(base64Images); // Base64 데이터 저장
            console.log("Generated Image (Base64):", base64Images); // 디버깅용

        } catch (error) {
            console.error(error.response?.data || error);
            alert("이미지 생성 중 에러 발생: " + (error.response.data.error.message || "알 수 없는 오류"));
            setError('이미지 생성 중 오류가 발생했습니다.');
        } finally {
            setLoading(false);
        }
    };

    //뜻과 예문 클릭 시 ----------------------------------------------------------------------------------------------
    const handleClickMeaning = (index) => {
        if (activeMeaningIndex === index) {
            // 이미 선택된 경우 선택 해제
            setActiveMeaningIndex(null);
            setSelectedMeaning(null); // 선택된 뜻도 해제
        } else {
            // 새로운 단어를 선택한 경우
            setActiveMeaningIndex(index);
            setSelectedMeaning(filteredMeanings[index]); // 선택된 뜻 업데이트
        }
    };
    const handleClickSentence = (index) => {
        if (activeSentenceIndex === index) {
            setActiveSentenceIndex(null);
            setSelectedSentence(null);
        } else {
            setActiveSentenceIndex(index);
            setSelectedSentence(filteredExSentence[index]);
        }
    };

    //뜻과 문장 가져와서 표시 ----------------------------------------------------------------------------------------------
    const filteredMeanings = Object.keys(currentData || {})
    .filter((key) => key.startsWith('뜻')) // key 변수를 사용하기 위해 적절히 매개변수로 선언
    .flatMap((key) => {
        const value = currentData[key];
        if (Array.isArray(value)) {
            return value;
        } else if (typeof value === 'string') {
            return value.split(',');
        }
        return [];
    })
    .filter((meaning) => meaning && meaning.trim() !== 'null');

    const filteredExSentence = Object.keys(currentData || {})
    .filter((key) => key.startsWith('예문')) // key 변수를 사용하기 위해 적절히 매개변수로 선언
    .flatMap((key) => {
        const value = currentData[key];
        if (Array.isArray(value)) {
            return value;
        } else if (typeof value === 'string') {
            const result = value.split(/[.?]/);
        }
        return [];
    })
    .filter((sentence) => sentence && sentence.trim() !== 'null')
    .concat(newSentences); // 새로 추가된 예문 포함


    //이미지 선택 - 선택된 이미지 저장 + 전체 데이터 업데이트 ----------------------------------------------------------------------------------------------
    const handleImageSelect = (url) => {
        setSelectedImage(url); // 선택된 이미지 URL 저장
    
        // fullData에서 현재 데이터를 업데이트
        setAllData((prevFullData) => {
            const updatedData = [...prevFullData];
            const currentIndex = updatedData.findIndex(
                (item) => item.단어 === currentData.단어
            );
    
            if (currentIndex !== -1) {
                updatedData[currentIndex] = {
                    ...updatedData[currentIndex],
                    이미지: url,
                };
            }
    
            return updatedData;
        });
    };
    useEffect(() => {
        console.log("Updated fullData:", allData);
    }, [allData]);  // fullData가 변경될 때마다 실행

    //새 예문 추가처리 ------------------------------------------------------------------------------------------------------------------
    const handleNewSentenceChange = (e) => {
        setNewSentence(e.target.value); // 새 예문 입력 상태 업데이트
        setActiveSentenceIndex(null); // 클릭한 예문의 index 저장
        setSelectedSentence(e.target.value); // 선택된 예문 업데이트

    };
    const handleAddSentence = () => {
        if (!newSentence.trim()) { // 입력 값이 없거나 공백만 있는 경우
            alert("예문 내용을 입력하세요."); // 사용자에게 알림
            return; // 함수 종료
        }
    
        // newSentences 배열에 새 예문 추가
        setNewSentences([...newSentences, newSentence]);
        setNewSentence(''); // 입력 필드 초기화
    };

    //특정 이미지만 다시 생성 - 이거 base64로 업데이트 해야됨 ------------------------------------------------------------------------------------------------------------------
    const regenerateImage = async (index) => {
        if (!currentData) {
            setError('단어가 전달되지 않았습니다.');
            return;
        }
    
        // 동일한 프롬프트 사용 (n: 1로 설정)
        const prompt = `
            You are an expert at creating detailed, colorpencil cartoon illustration image for AI-generated artwork. Your image should include the following details:
            -The central subject or scene that represents the meaning of the word.
            -The setting or background where the word might naturally appear.
            -Ensure the image is clear, specific, and focuses on accurately depicting the word's meaning.
            -Avoid adding any text or words in the image. Now, generate a image for the given word: "${currentData.단어}".
            ${selectedMeaning ? `-The meaning of this word is '${selectedMeaning}'. ` : ''}
            ${selectedSentence ? `-An example sentence for this word is: '${selectedSentence}'. ` : ''}
        `;
    
        console.log("Regenerated prompt:", prompt); // 프롬프트 확인용 콘솔 로그
        setLoading2(true);
    
        try {
            const response = await axios.post(
                'https://api.openai.com/v1/images/generations',
                {
                    model: "dall-e-3",
                    prompt,
                    n: 1, // 단일 이미지 생성
                    size: '1024x1024',
                    response_format: 'b64_json' // base64 형식으로 응답 받기
                },
                {
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: `Bearer ${apiKey}`,
                    },
                }
            );
    
            const base64Image = response.data.data[0].b64_json; // base64 이미지 데이터
    
            // 이미지 URL 대신 base64 데이터를 저장
            setImages((prevImages) =>
                prevImages.map((img, idx) => (idx === index ? base64Image : img))
            );


        } catch (err) {
            console.error(err);
            alert(err.message || 'An error occurred!');
            setError('이미지 다시 생성 중 오류가 발생했습니다.');
        } finally {
            setLoading2(false);
        }
    };
    
    //번호 이동 ------------------------------------------------------------------------------------------------------------------
    const handlePrev = () => {
        setCurrentIndex((prev) => Math.max(prev - 1, 0)); // 최소값 0
        setActiveMeaningIndex(null);
        setActiveSentenceIndex(null);
        setSelectedSentence('');
        setImages([]); // 번호 이동 시 이미지 초기화
    };
    const handleNext = () => {
        setCurrentIndex((prev) => Math.min(prev + 1, allData.length - 1)); // 최대값
        setActiveMeaningIndex(null);
        setActiveSentenceIndex(null);
        setSelectedSentence('');
        setImages([]); // 번호 이동 시 이미지 초기화
    };

    //선택한 이미지 저장 ------------------------------------------------------------------------------------------------------------------
    const saveImage = (index) => {
        const base64Image = images[index]; // 선택된 이미지의 base64 데이터
        const byteCharacters = atob(base64Image); // base64 문자열을 바이너리로 디코딩
        const byteArrays = [];
    
        // base64 데이터를 바이트 배열로 변환
        for (let offset = 0; offset < byteCharacters.length; offset += 1024) {
            const slice = byteCharacters.slice(offset, offset + 1024);
            const byteNumbers = new Array(slice.length);
            for (let i = 0; i < slice.length; i++) {
                byteNumbers[i] = slice.charCodeAt(i);
            }
            byteArrays.push(new Uint8Array(byteNumbers));
        }
    
        // Blob을 사용하여 이미지 데이터를 파일로 변환
        const blob = new Blob(byteArrays, { type: 'image/png' });
        const url = URL.createObjectURL(blob);
    
        // 링크를 생성하여 파일 다운로드
        const a = document.createElement('a');
        a.href = url;
        a.download = `${currentData.단어}_${index+1}.png`; // 파일 이름 지정
        a.click(); // 다운로드 실행
    
        // URL 객체 해제
        URL.revokeObjectURL(url);
    };

    return (
        <Container>
            <ViewImage>
                {loading
                    ? Array(4) // 로딩 중일 때 4개의 빈 영역 표시
                        .fill(null)
                        .map((_, index) => (
                            <ImageWrapper key={index}>
                            </ImageWrapper>
                        ))
                    : images.length > 0
                    ? images.map((base64, index) => (
                        <ImageWrapper key={index}>
                            <GeneratedImg
                                src={`data:image/png;base64,${base64}`} // Base64 데이터를 이미지로 렌더링
                                alt={`Generated ${index + 1}`}
                                isSelected={selectedImage === base64} // 선택 여부 전달
                                onClick={() => handleImageSelect(base64)}
                            />
                            <ButtonContainer>
                                <ImageButton
                                    isVisible={selectedImage === base64} // 선택된 이미지에서만 버튼 보이기
                                    onClick={() => regenerateImage(index)}
                                >
                                    <img src={process.env.PUBLIC_URL + '/retry.png'} width="24px"/>
                                    {loading2 ? '생성 중..' : '다시생성'}
                                </ImageButton>
                                <ImageButton
                                    isVisible={selectedImage === base64}
                                    onClick={() => saveImage(index)}
                                >
                                    <img src={process.env.PUBLIC_URL + '/download.png'} width="24px"/>
                                    저장하기
                                </ImageButton>
                            </ButtonContainer>
                        </ImageWrapper>
                    ))
                    : Array(4) // 로딩 전 빈 상태일 때 4개의 빈 영역 표시
                        .fill(null)
                        .map((_, index) => (
                            <ImageWrapper key={index}></ImageWrapper>
                        ))
                }
            </ViewImage>


            <InputData>
                <BoxContainer>
                    <Box>
                        <SectionText>번호</SectionText>
                        <ContentWrapperContainer>
                            <ContentWrapper>{currentIndex+1}</ContentWrapper>
                            <ContentWrapperStepper onClick={handlePrev}>←</ContentWrapperStepper>
                            <ContentWrapperStepper onClick={handleNext}>→</ContentWrapperStepper>
                        </ContentWrapperContainer>
                    </Box>
                    <Box>
                        <SectionText>단어</SectionText>
                        <ContentWrapper>
                            {currentData.단어 ? (
                                <Word>{currentData.단어}</Word>
                            ) : (
                                <Word>단어가 전달되지 않았습니다.</Word>
                            )}
                        </ContentWrapper>
                    </Box>
                    <Box>
                        <SectionText>뜻</SectionText>
                        <Meaningul>
                            {filteredMeanings.map((item, index) => (
                                <Meaningli
                                    key={index}
                                    onClick={() => handleClickMeaning(index)} // 뜻 클릭 시 처리
                                    style={{
                                        backgroundColor: activeMeaningIndex === index ? '#4B39EF' : '',
                                        color: activeMeaningIndex === index ? 'white' : '',
                                        fontWeight: activeMeaningIndex === index ? 'bold' : '',
                                        cursor: 'pointer',
                                    }}
                                >
                                    {item}
                                </Meaningli>
                            ))}
                        </Meaningul>
                    </Box>
                    <Box style={{height: '180px'}}>
                        <SectionText>예문</SectionText>

    
                        <ExSentenceArea>
                            {filteredExSentence.length > 0 && (
                                filteredExSentence.map((sentence, index) => (
                                    <ExSentence 
                                        key={index} 
                                        onClick={() => handleClickSentence(index)} // 예문 클릭 시 처리
                                        style={{
                                            backgroundColor: activeSentenceIndex === index ? '#4B39EF' : '', // 선택된 예문 배경색
                                            color: activeSentenceIndex === index ? '#FFFFFF' : '', // 선택된 예문 글자색
                                            cursor: 'pointer',
                                        }}
                                    >
                                        {sentence}
                                    </ExSentence>
                                ))
                            )}
                        </ExSentenceArea>
                        <AddSentenceContainer>        
                        <NewSentenceInput
                            id="new-sentence"
                            type="text"
                            value={newSentence}
                            onChange={handleNewSentenceChange}
                            placeholder="예문을 입력하세요."
                        />
                        <AddSentenceBtn onClick={handleAddSentence}>+</AddSentenceBtn>
                        </AddSentenceContainer>
                    </Box>
                </BoxContainer>

                <ButtonWrapper>
                    <ButtonMake onClick={generateImages} disabled={loading}>
                        {loading ? '생성 중...' : '이미지 만들기'}
                        {error && <p style={{ color: 'red' }}>{error}</p>}
                    </ButtonMake>
                    <ImgButtonMake
                        onClick={() => {
                            if (!selectedImage) {
                                alert("이미지를 선택하세요.");
                                return;
                            }
                            setOverlay(true); // 이미지를 선택한 경우에만 setOverlay(true)
                        }}>
                        단어장 만들기
                    </ImgButtonMake>
                </ButtonWrapper>
            </InputData>

            {/* VocabularyImg 컴포넌트에 선택된 이미지와 단어 전달 */}
            {overlay && selectedImage && (
                <Overlay>
                    <VocabularyImg
                        word={currentData.단어}
                        imageUrl={selectedImage}
                        meanings={[
                            currentData.뜻 || null,
                        ].filter(Boolean)}
                        sentence={selectedSentence}
                        closeOverlay={() => setOverlay(false)}
                    />
                </Overlay>
            )}

        </Container>
    );
}

export default GenerateImg;